import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons';
import { Link, NavLink } from 'react-router-dom';
import '../assets/styles/Navigation.css';

const Navigation = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const toggleMenu = () => {
        setIsMenuOpen(!isMenuOpen);
    };

    const closeMenu = () => {
        setIsMenuOpen(false);
    };

    return (
        <>
            <button className="mobile-nav-toggle" onClick={toggleMenu} aria-label="Toggle navigation menu">
                <FontAwesomeIcon icon={faBars} />
            </button>
            
            {isMenuOpen && (
                <div className="mobile-nav-overlay" onClick={closeMenu}></div>
            )}
            
            <nav className={`navigation ${isMenuOpen ? 'active' : ''}`}>
                <button className="close-nav" onClick={closeMenu} aria-label="Close navigation menu">
                    <FontAwesomeIcon icon={faTimes} />
                </button>
                <ul>
                    <li>
                        <NavLink to="/" onClick={closeMenu}>Home</NavLink>
                    </li>
                    <li>
                        <NavLink to="/services" onClick={closeMenu}>Services</NavLink>
                    </li>
                    <li>
                        <NavLink to="/about" onClick={closeMenu}>About</NavLink>
                    </li>
                    <li>
                        <NavLink to="/contact" onClick={closeMenu}>Contact</NavLink>
                    </li>
                </ul>
            </nav>
        </>
    );
};

export default Navigation;