import React from 'react';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import Navigation from './Navigation';
// Import Theme CSS first to ensure it loads before Header styles
import '../assets/styles/Theme.css';
import '../assets/styles/Header.css';
import logo from '../assets/images/logo.svg';

const Header = () => {
    const location = useLocation();
    const currentPage = location.pathname === '/' ? 'home' : location.pathname.substring(1);

    return (
        <header className={`site-header ${currentPage}-page-header`} id="main-header">
            <div className="container header-container">
                <Link to="/" className="logo">
                    <img src={logo} alt="Lawn and Garden Service" />
                    <span className="company-name">Petrichor Gardening</span>
                </Link>
                <Navigation />
                <div className="contact-info">
                    <a href="tel:+27730420424" className="phone-number">
                        <i className="fas fa-phone"></i>
                        <span>+27 (073) 042-0424</span>
                    </a>
                </div>
            </div>
        </header>
    );
};

export default Header;