import React from 'react';
import { Helmet } from 'react-helmet-async';

const LocalBusinessSchema = () => {
  const schema = {
    "@context": "https://schema.org",
    "@type": "LandscapingBusiness",
    "name": "Petrichor Gardening & Irrigation Services",
    "description": "Professional garden maintenance, irrigation systems and pool services in Centurion & Pretoria East. We provide weekly garden service, pool cleaning, and irrigation system installation and maintenance.",
    "image": "https://www.petrichorgardening.co.za/logo.png",
    "url": "https://www.petrichorgardening.co.za",
    "telephone": "+27730420424",
    "email": "petrichor.gardening@gmail.com",
    "address": {
      "@type": "PostalAddress",
      "streetAddress": "90 River Road",
      "addressLocality": "Lyttleton",
      "addressRegion": "Centurion",
      "postalCode": "0157",
      "addressCountry": "ZA"
    },
    "geo": {
      "@type": "GeoCoordinates",
      "latitude": "-25.841092309124306",
      "longitude": "28.21127424093883"
    },
    "areaServed": {
      "@type": "GeoCircle",
      "geoMidpoint": {
        "@type": "GeoCoordinates",
        "latitude": "-25.841092309124306",
        "longitude": "28.21127424093883"
      },
      "geoRadius": "30000"
    },
    "openingHoursSpecification": [
      {
        "@type": "OpeningHoursSpecification",
        "dayOfWeek": ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"],
        "opens": "08:00",
        "closes": "17:00"
      }
    ],
    "priceRange": "$$",
    "sameAs": [
      "https://www.facebook.com/PetrichorGardening",
      "https://www.instagram.com/petrichorgardening"
    ],
    "serviceType": ["Garden Maintenance", "Pool Maintenance", "Irrigation Systems", "Landscape Design"]
  };

  return (
    <Helmet>
      <script type="application/ld+json">
        {JSON.stringify(schema)}
      </script>
    </Helmet>
  );
};

export default LocalBusinessSchema;